<template>
  <div id="templateModule">
    <Location v-bind:locations="locations" v-on:clickLocation="clickLocation"/>
    <keep-alive>
      <router-view/>
    </keep-alive>
    <div style="width: 100%;height: 20px"></div>
  </div>
</template>

<script>

  import Location from '../../components/location/Location'

  export default {
    name: "templateModule",
    components:{
      Location,
    },
    data:function () {
      return {
        show:false,
        locations:[''],
        locationPaths:['templateList'],
      }
    },
    watch:{
      $route (to, from) {

        let params=to.params;

        let toName=to.name;

        let fromName=from.name;

        let locationPaths=this.locationPaths;

        let locations=this.locations;

        let isPre=(locationPaths.indexOf(fromName)>=0&&locationPaths.indexOf(toName)<0)||!fromName;

        let isBack=params.isBack||(locationPaths.indexOf(fromName)>locationPaths.indexOf(toName)&&locationPaths.indexOf(toName)>=0);

        let isNav=params.isNav;

       if (isBack&&!isNav){

          if (!toName){

            locationPaths.splice(0,locationPaths.length-1);

            locations.splice(0,locations.length-1);
          }
          else {

            let toIndex=locationPaths.indexOf(toName);

            locationPaths.splice(toIndex+1,locationPaths.length-toIndex-1);

            locations.splice(toIndex+1,locations.length-toIndex-1);

          }

        }
        else if (isPre){

          if (toName=='addTemplate'){

            locationPaths.push(toName);

            let title='';

            if (params&&params.title){

              title=params.title;
            }

            locations.push(title);
          }
          else if (toName=='addFlow'){

            locationPaths.push(toName);

            locations.push('');
          }
          else if (toName=='editTemplate'){

            locationPaths.push(toName);

            locations.push('');
          }
          else if (toName=='templateDetail'){

            locationPaths.push(toName);

            locations.push('');
          }

        }



      }
    },
    mounted:function(){

    },
    beforeRouteEnter:function (to,from,next) {
      next(vm => {
        //  `vm` 
        vm.$emit('goToMenuItem',0);

      })

    },
    methods:{

      /**
       * 
       *
       */
      clickLocation:function(index){

        if (index<this.locationPaths.length-1){

          let name=this.locationPaths[index];

          this.$router.push({
            name:name,
            params:{
              isNav:true,
              isBack:true,
            }
          });

          this.locations.splice(index+1,this.locations.length-index);

          this.locationPaths.splice(index+1,this.locationPaths.length-index);

        }

      },

    }
  }
</script>

<style scoped>

  #templateModule{
    width: 100%;
  }
</style>



