import {BaseRequest,sendRequest} from "../../networking/request/oeRequest";

import objectParse from '../../util/objectParse';

export function Department(){
  this.id=0;
  this.departmentName=null;
  this.userList=null;
}

function User() {
  this.id=0;
  this.nickname=null;
  this.username=null;
  this.phoneno=null;
  this.departmentName=null;
  this.position=null;
  this.status=0;
}

/**
 * 
 *
 * @param departmentId
 * @param userType
 */
export function loadTeamMemberList(callBack) {

  let request=new BaseRequest('getMyArchitecture',{
    type:2
  },null);

  request.success=function(baseResponse){

    let departmentList=baseResponse.msgData?baseResponse.msgData.departments:null;

    let list=[];

    if (departmentList&&departmentList instanceof Array){

      for (let i=0;i<departmentList.length;i++){

        let department=new Department();

        department=Object.assign(department,departmentList[i]);

        if (department.userList&&department.userList instanceof Array){

          let userList=objectParse.objectArrayFormKeyValuesArray(department.userList,function () {

            let user=new User();

            user.departmentName=department.departmentName;

            return user;

          });

          list=list.concat(userList);
        }

      }
    }
    
    if (callBack){

      callBack(true,list,baseResponse);
    }
  };

  request.fail=function(baseResponse){

    if (callBack){

      callBack(false,null,baseResponse);
    }

  };

  sendRequest(request);
}

export function listOfPageWithPageSize(list,page,pageSize) {

  let totalPage=Math.ceil(list.length/pageSize);

  if (page>totalPage){

    return null;
  }
  else {

    let startIndex=(page-1)*pageSize;

    let endIndex=startIndex+pageSize;

    let newList=[];

    for (let i=startIndex;i<endIndex&&i<list.length;i++){

      newList.push(list[i]);
    }

    return newList;
  }

}





