import {BaseRequest,sendRequest} from "../../networking/request/oeRequest";

import {chineseToPinYin} from "../../util/chinese2pinyin";


/**
 * 
 *
 * @param departmentId
 * @param userType
 */
export function loadDepartments(callBack) {

  let request=new BaseRequest('getDepartmentWithPosition',{

  },null);

  request.success=function(baseResponse){

    let departmentList=baseResponse.msgData;

    let list=[];

    if (departmentList&&departmentList instanceof Array){

      for (let i=0;i<departmentList.length;i++){

        let department=new Department();

        department=Object.assign(department,departmentList[i]);

        list.push(department);
      }
    }

    if (callBack){

      callBack(true,list,baseResponse);
    }
  };

  request.fail=function(baseResponse){

    if (callBack){

      callBack(false,null,baseResponse);
    }

  };

  sendRequest(request);
}

export function Department(){
  this.id=0;
  this.departmentName=null;
}


/**
 * 
 *
 * @param page
 * @param pageSize
 * @param callback
 */
export function loadTemplateList(page,pageSize,callBack) {

  let request=new BaseRequest('getModelList',{
    type:2
  },null);

  request.timeout=15000;

  request.success=function(baseResponse){

    let templateList=baseResponse.msgData;

    let list=[];

    if (templateList&&templateList instanceof Array){

      for (let i=0;i<templateList.length;i++){

        let template=new TemplateModel();

        template=Object.assign(template,templateList[i]);

        list.push(template);
      }
    }

    if (callBack){

      callBack(true,list,baseResponse);
    }
  };

  request.fail=function(baseResponse){

    if (callBack){

      callBack(false,null,baseResponse);
    }

  };

  sendRequest(request);

}

export function templateListOfKeywords(templateList,keywords){

  let list=[];

  for (let i=0;i<templateList.length;i++){

    let template=templateList[i];

    if (template.name.indexOf(keywords)!= -1){

      list.push(template);
    }

  }

  return list;
}


export function listOfPageWithPageSize(list,page,pageSize) {

  let totalPage=Math.ceil(list.length/pageSize);

  if (page>totalPage){

    return null;
  }
  else {

    let startIndex=(page-1)*pageSize;

    let endIndex=startIndex+pageSize;

    let newList=[];

    for (let i=startIndex;i<endIndex&&i<list.length;i++){

      newList.push(list[i]);

    }

    return newList;
  }

}

/**
 * 
 *
 * @param template 
 * @param callBack 
 */
export function createTemplate(template,callBack) {

  let flowList=[];

  for (let i=0;i<template.flowList.length;i++){

    let flowModel=new FlowModel(template.flowList[i],i+1);

    flowList.push(flowModel);
  }

  let request=new BaseRequest('addModel',{
    name:template.name,
    type:template.type,
    description:template.description,
    departmentId:template.departmentId,
    model:JSON.stringify(flowList)
  },null);


  request.success=function(baseResponse){

    let templateList=baseResponse.msgData;

    let list=[];

    if (templateList&&templateList instanceof Array){

      for (let i=0;i<templateList.length;i++){

        let template=new TemplateModel();

        template=Object.assign(template,templateList[i]);

        list.push(template);
      }
    }

    if (callBack){

      callBack(true,list,baseResponse);
    }

  };

  request.fail=function(baseResponse){

    callBack(false,null,baseResponse);
  };

  sendRequest(request);

}

