/**
 * 
 *
 * @param flowId
 * @param callBack
 */
export function modifyFlowOrderOfId(flowId,newOrder,callBack) {

  let request=new BaseRequest('updatePosition',{
    id:flowId,
    newLevel:newOrder
  },null);

  request.success=function(baseResponse){

    let flowList=[];

    if (baseResponse&&baseResponse.msgData&&baseResponse.msgData instanceof Array){

      let flowArray=baseResponse.msgData;

      //
      for (let i=0;i<flowArray.length;i++){

        let flowModel=flowArray[i]

        let flow=new FlowDetailModel();

        flow=Object.assign(flow,flowModel);

        let programProcesses=[];

        //
        if (flowModel.programProcesses&&flowModel.programProcesses instanceof Array){

          for (let j=0;j<flowModel.programProcesses.length;j++){

            let process=new ProcessDetailModel();

            process=Object.assign(process,flowModel.programProcesses[j]);

            programProcesses.push(process);
          }
        }

        flow.programProcesses=programProcesses;

        flowList.push(flow);

      }

    }

    if (callBack){

      callBack(true,flowList,baseResponse);
    }

  };

  request.fail=function(baseResponse){

    if (callBack){
      callBack(false,null,baseResponse);
    }
  };

  sendRequest(request);
}

/**
 * 
 *
 * @param flowId
 * @param flowModel
 * @param callBack
 */
export function addOrUpdateFlow(flowModel,callBack) {

  let flow={
    name:flowModel.flowName,
    type:flowModel.type,
  };

  if (flowModel.id>0){
    flow.id=flowModel.id;
  }

  if (flowModel.modelId>0){

    flow.modelId=flowModel.modelId;
  }

  if (flowModel.type==2){

    flow.departmentId=flowModel.departmentId;
  }

  flow.docHead=documentPrefixName(flow.name,flowModel.level);

  let jsonProgramProcesses=[];

  for (let i=0;i<flowModel.processList.length;i++){

    let process=flowModel.processList[i];

    let newProcess={
      name:process.name,
      defaultChargePersion:process.response.userId,
      defaultExamPersion:process.review.userId,
      defaultTimeSpan:process.defaultTimeSpan,
    }

    jsonProgramProcesses.push(newProcess);

  }

  flow.jsonProgramProcesses=JSON.stringify(jsonProgramProcesses);

  let request=new BaseRequest('addOrUpdateFlowModel',flow,null);


  request.success=function(baseResponse){

    let flowList=[];

    if (baseResponse&&baseResponse.msgData&&baseResponse.msgData instanceof Array){

      let flowArray=baseResponse.msgData;

      //
      for (let i=0;i<flowArray.length;i++){

        let flowModel=flowArray[i]

        let flow=new FlowDetailModel();

        flow=Object.assign(flow,flowModel);

        let programProcesses=[];

        //
        if (flowModel.programProcesses&&flowModel.programProcesses instanceof Array){

          for (let j=0;j<flowModel.programProcesses.length;j++){

            let process=new ProcessDetailModel();

            process=Object.assign(process,flowModel.programProcesses[j]);

            programProcesses.push(process);
          }
        }

        flow.programProcesses=programProcesses;

        flowList.push(flow);

      }

    }

    if (callBack){

      callBack(true,flowList,baseResponse);
    }

  };

  request.fail=function(baseResponse){

    callBack(false,baseResponse);
  };

  sendRequest(request);

}

