export function FlowModel(flow,index) {

  //flow
  this.name=flow.flowName;

  //flow
  this.level=index;

  this.docHead=documentPrefixName(flow.flowName,index);

  //
  let newProgramProcesses=[];

  for (let i=0;i<flow.processList.length;i++){

    let process=flow.processList[i];

    let processModel=new ProcessModel(process);

    newProgramProcesses.push(processModel);
  }

  this.newProgramProcesses=newProgramProcesses;

}

export function ProcessModel(process) {

  this.name=process.name;

  this.defaultTimeSpan=process.defaultTimeSpan;

  if (this.defaultTimeSpan<0||!this.defaultTimeSpan){

    this.defaultTimeSpan=0;
  }

  this.defaultChargePersion=process.response.userId;

  this.defaultExamPersion=process.review.userId;

}

