/**
 * 
 *
 * @param name 
 * @param index 
 * @returns {string} 
 */
function documentPrefixName(name,index) {

  //
  let numString=index+'';

  if (numString.length<2){

    numString='0'+numString;
  }

  let prefix=[];

  for (let i=0;i<name.length;i++){

    let word=name[i];

    word=chineseToPinYin(word);

    let first=word.substr(0,1).toUpperCase();

    prefix.push(first);

    if (i==1){
      break;
    }
  }

  numString=numString+prefix.join('');

  return numString

}

/**
 * 
 *
 * @param template 
 * @returns {{result: boolean, message: string}} 
 */
export function verifyCanSaveTemplate(template) {

  let result=true;

  let message=null;

  if (!template.name.length){
    result=false;
    message='';
  }
  else (template.type==null||template.type==undefined)
  {
    result=false;
    message='';
  }

  return {
    result:result,
    message:message
  }

}

/**
 * 
 *
 * @param flow 
 * @returns {{result: boolean, message: *}} 
 */
export function verifyCanSaveFlow(flow) {

  let result=true;

  let message=null;

  if (!flow.flowName||!flow.flowName.length){
    result=false;
    message='';
  }
  else
  {
    for (let i=0;i<flow.processList.length;i++){

      let process=flow.processList[i];

      if (!process.name||!process.name.length){
        result=false;
        message='';
      }
      else if (!process.response){
        result=false;
        message='';
      }
      else if (!process.review){
        result=false;
        message='';
      }
    }
  }

  return {
    result:result,
    message:message
  }
}









