
function setKeyValuesFrom(object,fromObject) {


    for (var item in object) {

        //
        if (typeof (object[item]) !='function'){

            //
            var ignoredKeys=[];

            if (object.ignoredKeys) {

                ignoredKeys=object.ignoredKeys();
            }

            var ignored=false

            try {

                if (ignoredKeys.indexOf(item)>=0){

                    ignored=true;
                }
            }
            catch (e) {

                console.log('ignoredKeys')
            }

            if (ignored){
                continue;
            }

            //
            if (fromObject&&fromObject[item]!=undefined){

                // 
                if (object.customObjectOfKey&&typeof object.customObjectOfKey =='function'&&object.customObjectOfKey(item)){

                    var customObject=object.customObjectOfKey(item);

                    if (customObject){

                        object[item]=setKeyValuesFrom(customObject,fromObject[item]);
                    }
                    else {
                        object[item]=fromObject[item];
                    }
                }
                else if(object.arrayObjectOfKey&&typeof object.arrayObjectOfKey =='function'&&object.arrayObjectOfKey(item)){//


                    if (fromObject[item] instanceof Array){

                        var objectArray=[];

                        var fromObjectArray=fromObject[item]

                        for (var i=0;i<fromObjectArray.length;i++){

                            var customObject=object.arrayObjectOfKey(item);

                            setKeyValuesFrom(customObject,fromObjectArray[i]);

                            objectArray.push(customObject);
                        }

                        object[item]=objectArray;

                    }
                    else {

                        object[item]=null;
                    }
                }
                else {

                    object[item]=fromObject[item];
                }

            }
            else {

                if (object.undefinedValueOfKey&&typeof object.undefinedValueOfKey =='function'){

                    object[item]=object.undefinedValueOfKey(item);
                }
            }
        }
    }

    if (object.setKeyValuesCompletion&& typeof object.setKeyValuesCompletion == 'function'){

        object.setKeyValuesCompletion();
    }
}

/**
 * @param keyValuesArray 
 * @param objectCompletion 
 * @constructor 
 */
function objectArrayFormKeyValuesArray(keyValuesArray,objectCompletion){

    if (keyValuesArray&&keyValuesArray instanceof Array){

        var array=[];

        for (var i=0;i<keyValuesArray.length;i++){

            var object=null;

            if (objectCompletion&&typeof objectCompletion=='function'){

                object=objectCompletion();
            }

            if (!object){

                return keyValuesArray;
            }

            var keyValue=keyValuesArray[i];

            setKeyValuesFrom(object,keyValue);

            array.push(object);
        }

        return array;

    }
    else {
        return null;
    }
}


module.exports.setKeyValuesFrom = setKeyValuesFrom;

module.exports.objectArrayFormKeyValuesArray = objectArrayFormKeyValuesArray;


