<template>
  <div class="add-flow-wrap">

    <div class="add-flow-content" v-bind:class="{forbidEdit:forbidEdit}">
      <div class="add-flow-title">{{flowTitle}}</div>
      <div class="flow-title"></div>
      <input class="flow-title-input" type="text" placeholder="" v-model="flowName">
      <div class="process-set-title"></div>
      <div v-for="(item,index) in processList" class="process-wrap" >
        <input class="process-name-input" type="text" placeholder="" v-model="item.name">
        <div class="process-person-chose"
             v-bind:class="{hasResponse:item.person&&item.person.length>0}"
             v-on:click="clickChoseResponse(index,$event)">
          <div v-if="item.response?false:true" style="color: #BBBBBB;"></div>
          <div v-if="item.response" class="personTitle"></div>
          <div v-if="item.response" class="personName">{{item.response.userName}}</div>
        </div>
        <div class="process-person-chose"
             v-bind:class="{hasResponse:item.person&&item.person.length>0}"
             v-on:click="clickChoseReview(index,$event)">
          <div v-if="item.review?false:true" style="color: #BBBBBB;"></div>
          <div v-if="item.review" class="personTitle"></div>
          <div v-if="item.review" class="personName">{{item.review.userName}}</div>
        </div>
        <button v-if="processList.length>1" class="btn-process-delete" v-on:click="clickedDeleteProcess(index)"></button>

      </div>
      <button class='btn-add-process' v-on:click="clickedAddProcess">+</button>
    </div>

    <div class="add-flow-btn-wrap">
      <button class="operate-button btn-save-flow" v-bind:disabled="forbidEdit" v-on:click="clickSaveFlow">{{saveButtonTitle}}</button>
      <button class="operate-button btn-cancel-flow" v-on:click="clickCancelFlow"></button>
    </div>

    <ChoseMember v-if="showChoseMember"
                 v-on:choseMemberCompletetion="choseMemberCompletetion"
                 v-on:cancelChoseMember="cancelChoseMember"
                 v-bind:o-type="oType"
                 v-bind:template-type="templateType" v-bind:default-person="defaultPerson"></ChoseMember>

  </div>
</template>

<script>

    import {FlowOperateType} from "../flow/FlowOperateType";

    import {PersonType} from "../choseMember/choseMemberManager";

    import ChoseMember from '../choseMember/ChoseMember';

    import Toast from '../../components/toast/Toast';

    import {addOrUpdateFlow} from "../templateList/templateManager";

    export default {
      name: "Flow",
      data: function () {
        return {
          show: false,
          showChoseMember:false,
          flowTitle: '',
          operateType: FlowOperateType.newTemplateAddFlow,
          flowModel: null,
          flowName: null,
          processList: [{
            name: null,
            response: null,
            review: null,
            defaultTimeSpan: 0
          }],
          oType:PersonType.PersonTypeExecutor,
          operateFlowCompletion: null,
          templateType:null,
          defaultPerson:null,
          saveButtonTitle:'',
          forbidEdit:false,
        }
      },
      components:{
        ChoseMember
      },
      mounted: function () {

      },
      activated:function(){
        let params=this.$route.params;
        if (params.isBack){
          return;
        }
        this.resetData();

        if (params){
          this.operateType=params.operateType;
          this.templateType=params.templateType;
          if (params.flow){
            console.log('params.flow')
            this.setFlowModelAndType(params.flow,this.operateType);
          }
        }
      },

      methods: {
        resetData: function () {
          this.showChoseMember = false;
          this.flowTitle= '';
          this.operateType = FlowOperateType.newTemplateAddFlow;
          this.flowModel=null;
          this.flowName = null;
          this.processList = [{
            name: null,
            response: null,
            review: null,
            defaultTimeSpan: 0
          }];
          this.oType=PersonType.PersonTypeExecutor;
          this.operateFlowCompletion=null;
          this.defaultPerson=null;
          this.forbidEdit=false;
          this.saveButtonTitle='';
        },

        /**
         *
         * flow
         *
         */
        setFlowModelAndType: function (flowModel, operateType) {

          let flowTitle='';

          switch (operateType) {
            case FlowOperateType.newTemplateAddFlow:
              flowTitle='';
              break;
            case FlowOperateType.newTemplateEditFlow:
              flowTitle='';
              break;
            case FlowOperateType.editTemplateAddFlow:
              flowTitle='';
              break;
            case FlowOperateType.editTemplateEditFlow:
              flowTitle='';
              break;
            default:
              break;
          }

          this.flowTitle=flowTitle;

          this.flowName = null;

          this.processList = [{
            name: null,
            defaultTimeSpan: 0,
            response: null,
            review: null,
          }];

          if (flowModel) {

            if (flowModel.flowName) {

              this.flowName = flowModel.flowName;
            }

            if (flowModel.processList) {

              let processList = [];

              for (let i = 0; i < flowModel.processList.length; i++) {

                let process = flowModel.processList[i];

                let newProcess = new Object();

                newProcess.name = process.name;

                newProcess.defaultTimeSpan = process.defaultTimeSpan;

                newProcess.response = process.response?process.response:null;

                newProcess.review = process.review?process.review:null;

                processList.push(newProcess);

              }

              this.processList = processList;
            }

          }


          this.flowModel = flowModel;

          this.operateType = operateType;

        },

        /**
         * 
         *
         */
        clickChoseResponse: function (index, event) {

          const that=this;

          let process = this.processList[index];

          this.oType=PersonType.PersonTypeExecutor;

          this.defaultPerson=process.response;

          this.showChoseMember=true;


          this.choseMemberCompletetion=function (person) {

            process.response = Object.assign({}, person);

            that.showChoseMember=false;

          }

          this.cancelChoseMember=function () {

            that.showChoseMember=false;

          }

        },

        /**
         * 
         *
         */
        clickChoseReview: function (index, event) {

          const that=this;

          let process = this.processList[index];

          this.oType=PersonType.PersonTypeReviewer;

          this.showChoseMember=true;

          this.defaultPerson=process.review;

          this.choseMemberCompletetion=function (person) {

            process.review = Object.assign({}, person);

            that.showChoseMember=false;

          }

          this.cancelChoseMember=function () {

            that.showChoseMember=false;

          }

        },

        /**
         * 
         *
         */
        clickedDeleteProcess: function (index) {

          this.processList.splice(index, 1);
        },

        /**
         * 
         *
         */
        clickedAddProcess: function () {

          let process = {
            name: null,
            response: null,
            review: null,
            defaultTimeSpan: 0
          };

          this.processList.push(process);

        },

        /**
         * 
         *
         */
        clickSaveFlow: function () {

          if (this.forbidEdit){
            return;
          }

          var flowModel = {
            flowName: this.flowName,
            processList: this.processList
          };

          //
          let canSave = this.verifyCanSaveFlow(flowModel);

          if (!canSave.result) {

            Toast.showToast(canSave.message);

            return;
          }

          let operateType=this.$route.params.operateType;

          let  operateFlowCompletion=this.$route.params.operateFlowCompletion;

          //>  >
          if (operateType == FlowOperateType.newTemplateAddFlow || operateType == FlowOperateType.newTemplateEditFlow) {

            this.show = false;

            if (operateFlowCompletion) {

              operateFlowCompletion(true,flowModel);
            }

          }
          else//>  >
          {
            flowModel={
              modelId: this.flowModel.modelId,
              flowName: this.flowName,
              type: this.flowModel.type,
              level: this.flowModel.level,
              departmentId:this.flowModel.departmentId,
              processList: this.processList
            };

            //>
            if (this.operateType == FlowOperateType.editTemplateEditFlow) {

              flowModel.id=this.flowModel.id;
            }

            const that=this;

            this.forbidEdit=true;

            this.saveButtonTitle='';

            addOrUpdateFlow(flowModel, function (isSuccess,flowList,baseResponse) {

              that.forbidEdit=false;

              that.saveButtonTitle='';

              if (isSuccess){

                if (operateFlowCompletion) {

                  let lastFlow=flowList[flowList.length-1];

                  if (!flowModel.id) {

                    flowModel.id=lastFlow.id;

                  }

                  operateFlowCompletion(true,flowModel);

                }
              }
              else {

                let title = '';

                if (that.operateType == FlowOperateType.editTemplateAddFlow) {

                  title = '';
                }

                if (baseResponse && baseResponse.msg) {

                  title = baseResponse.msg;
                }

                Toast.showToast(title);

              }

            })

          }

        },

        /**
         * 
         *
         */
        clickCancelFlow: function () {

          if (this.forbidEdit){

            this.forbidEdit=false;

            this.saveButtonTitle='';
          }

          let  operateFlowCompletion=this.$route.params.operateFlowCompletion;

          if (operateFlowCompletion){

            operateFlowCompletion(false,null);
          }

        },

        /**
         * 
         * @param flow
         * @returns {{result: boolean, message: *}}
         */
        verifyCanSaveFlow:function(flow) {

          let result=true;

          let message=null;

          if (!flow.flowName||!flow.flowName.length){
            result=false;
            message='';
          }
          else
          {
            for (let i=0;i<flow.processList.length;i++){

              let process=flow.processList[i];

              if (!process.name||!process.name.length){
                result=false;
                message='';
              }
              else if (!process.response){
                result=false;
                message='';
              }
              else if (!process.review){
                result=false;
                message='';
              }
            }
          }

          return {
            result:result,
            message:message
          }
        }

      }
    }
</script>

<style scoped>
  .add-flow-wrap{
    width: 100%;
    display: flex;
    min-width: 1000px;
    margin-top: 0px;
    flex-direction: column;
    z-index: 1000;
  }

  .add-flow-content{
    min-height: 520px;
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 15px 20px 50px;
  }

  .forbidEdit{
    pointer-events: none;
  }

  .add-flow-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
  }

  .flow-title{
    margin-top: 20px;
  }

  .process-set-title{
    margin-top: 10px;
  }

  .flow-title,.process-set-title{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    letter-spacing: 0;
    line-height: 18px;
  }

  .flow-title-input{
    width: 560px;
    margin-top: 5px;
    box-sizing: border-box;

  }


  .process-wrap{
    width: 620px;
    margin-top: 5px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-start;
  }

  .process-name-input{
    width: 280px;
    box-sizing: border-box;
    margin-right: 0px;
  }

  .flow-title-input,.process-name-input{
    height: 40px;
    background: #F4F4F4;
    border: none;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    padding: 0px 10px;
  }

  .process-person-chose{
    width: 130px;
    height: 40px;
    margin-left: 10px;
    background: #F4F4F4;
    text-align: left;
    padding:0px 10px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #BBBBBB;
    outline: none;
    border: none;
    box-sizing: border-box;
    cursor: pointer;
    line-height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: nowrap;
  }

  .process-person-chose .personTitle{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    letter-spacing: 0;
    white-space: nowrap;
  }

  .process-person-chose .personName{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #0C101B;
    letter-spacing: 0;
    margin-left: 15px;
    white-space: nowrap;
  }

  .process-person-chose:hover{
    background: rgba(0,0,0,0.10);
  }

  .process-person-chose:active{
    background: #F4F4F4;
  }


  .hasResponse{
    color: black;
  }

  .btn-process-delete{
    height: 40px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #0087E7;
    letter-spacing: 0;
    border: none;
    outline: none;
    background-color: transparent;
    margin-left: 15px;
  }

  .btn-process-delete:hover{
    color: #006eff;
    cursor: pointer;
  }

  .btn-process-delete:active{
    color: #0087E7;
  }

  .btn-add-process{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #0087E7;
    text-align: center;
    margin-top: 10px;
    outline: none;
    border: none;
    background-color: transparent;
  }

  .btn-add-process:hover{
    color: #006eff;
    cursor: pointer;
  }

  .btn-add-process:active{
    color: #0087E7;
  }

  .add-flow-btn-wrap{
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    margin-top: 20px;
  }

  .add-flow-btn-wrap .operate-button{
    width: 160px;
    height: 40px;
  }

  .add-flow-btn-wrap .btn-save-flow{
    background: #0087E7;
    border: none;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    text-align: center;
    outline: none;
  }

  .add-flow-btn-wrap .btn-save-flow:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .add-flow-btn-wrap .btn-save-flow:active{
    background-color: #0087E7;
  }

  .add-flow-btn-wrap .btn-cancel-flow{
    background: #D5D5D5;
    border: none;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    text-align: center;
    outline: none;
    margin-left: 20px;
  }

  .add-flow-btn-wrap .btn-cancel-flow:hover{
    background-color: rgba(0,0,0,0.25);
    cursor: pointer;
  }

  .add-flow-btn-wrap .btn-cancel-flow:active{
    background-color: #D5D5D5;
  }


</style>



