<template>
    <div id="location">
      <ul class="location-ul">
        <li class="location-li location"></li>
        <li v-for="(item,index) in locations" class="location-li location">
          <a href="javascript:void(0)" v-on:click="clickLocation(index)" class="location">{{item}}</a>
          <span v-if="index<locations.length-1">&gt;</span>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "Location",
        props:{
          locations:{
            default:[],
            type:Array
          },
        },
        methods:{
          clickLocation:function (index) {
            this.$emit('clickLocation',index);
          }
        }
    }
</script>

<style scoped>

  #location{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #616366;
    letter-spacing: 0;
    line-height: 20px;
  }

  .location-ul{
    margin-left: 20px;
    margin-top: 10px;
    list-style: none none ;
    display: inline-block;
    margin-bottom: 10px;
    padding-left: 0px;
  }

  .location-li{
    list-style: none none ;
    float: left;
    display: inline-block;
  }

  .location-li a{
    text-decoration: none;
    color: #616366;
  }

</style>



