<template>
  <div class="chose-response-mask">
    <div  class="chose-response-wrap">
      <div class="chose-response-header">
        <div class="chose-response-title">{{title}}</div>
        <button class="chose-response-btn chose-response-btn-sure" v-on:click="clickSureButton"></button>
        <button class="chose-response-btn chose-response-btn-cancel" v-on:click="clickCancelButton"></button>
      </div>
      <table>
        <tr>
          <th style="width: 110px"></th>
          <th style="width: 160px"></th>
          <th style="width: 140px"></th>
          <th style="width: 160px"></th>
          <th ></th>
          <th style="width: 100px"></th>
        </tr>
        <tr v-for="(person,index) in currentMemberList" class="chose-response-hover" v-on:click="selectPerson(index)">
          <td style="width: 110px">{{person.userName}}</td>
          <td style="width: 160px">{{person.phoneno}}</td>
          <td style="width: 140px">{{person.departmentName}}</td>
          <td style="width: 160px">{{person.position}}</td>
          <td>{{person.status==0||person.status==null?'':''}}</td>
          <td style="width: 100px" class="choseResponseUnSelected" v-bind:class="{choseResponseSelected:person.selected}"></td>
        </tr>
      </table>
      <PageControl class="bottom-page-bar" v-bind:current-page="currentPage" v-bind:total-page="totalPage"  v-on:clickedPrevious="clickedPrevious" v-on:clickedNext="clickedNext"/>

    </div>
  </div>
</template>

<script>
    import PageControl from '../../components/pageControl/PageControl'
    import {PersonType,getPersonListOfType} from "./choseMemberManager";

    import {listOfPageWithPageSize} from "../../util/arrayUtil";

    export default {
      name: "ChoseMember",
      components:{
        PageControl
      },
      data:function () {
        return {
          memberList:null,
          currentMemberList:null,
          currentPage:0,
          totalPage:0,
          pageSize:10,
          selectedMember:null,
        }
      },
      props:{
        defaultPerson:{
          type:Object,
          default:null
        }
      },
      computed:{
        title:function () {
          if (this.oType==PersonType.PersonTypeExecutor){
            return '';
          }
          else {
            return '';
          }
        }
      },
      props:{
        oType:{
          type:Number,
          default:PersonType.PersonTypeExecutor,
        },
        templateType:{
          type:Object,
          default:null,
        }
      },
      created:function(){
        this.getMemberList();
      },
      methods: {

        /**
         * 
         *
         */
        getMemberList:function () {

           const that=this;

           getPersonListOfType(this.templateType.id,this.oType,function (isSuccess,list,baseResponse) {

             if (isSuccess){

               if (list.length){

                 that.currentPage=1;

               }else {

                 that.currentPage=0;
               }

               that.memberList=list;

               let pageSize=that.pageSize;

               that.totalPage=Math.ceil(list.length/pageSize);

               that.show=true;

               that.currentMemberList=listOfPageWithPageSize(list,1,pageSize);

               that.setDefaultSelected();

             }
             else{

             }

           })

        },

        /**
         *
         * 
         *
         */
        setDefaultSelected:function(defaultPerson){

          let defaultId=defaultPerson?defaultPerson.userId:null;

          for (let i=0;i<this.memberList.length;i++){

            let person=this.memberList[i];

            if (defaultId&&person.userId==defaultId){

              person.selected=true;

              this.selectedMember=person;
            }
            else
            {
              person.selected=false;
            }

          }

        },

        /**
         * button
         *
         */
        clickSureButton:function(){

          let selectedPerson=this.selectedMember;

          if (selectedPerson){

            this.$emit('choseMemberCompletetion',selectedPerson);
          }

        },

        /**
         *
         * button
         *
         */
        clickCancelButton:function(){

          this.$emit('cancelChoseMember');

        },


        /**
         * 
         * @param index
         */
        selectPerson:function (index) {

          console.log(index);

          let selectedMember=null;

          for (let i=0;i<this.currentMemberList.length;i++){

            let person=this.currentMemberList[i];

            if (i==index){

              person.selected=!person.selected;

              if (person.selected){

                selectedMember=person;
              }
              else{

                selectedMember=null;
              }

            }
          }

          if (this.selectedMember){

            this.selectedMember.selected=false;

          }

          this.selectedMember=selectedMember;

          this.$nextTick();

        },

        /**
         * 
         *
         */
        clickedPrevious:function(){

          this.currentPage=Math.max(this.currentPage-1,1);

          this.currentMemberList=listOfPageWithPageSize(this.templateList,this.currentPage,this.pageSize);

        },

        /**
         * 
         *
         */
        clickedNext:function(){

          this.currentPage=Math.min(this.currentPage+1,this.totalPage);

          this.currentMemberList=listOfPageWithPageSize(this.templateList,this.currentPage,this.pageSize);
        }


      }


    }
</script>

<style scoped>
  .chose-response-mask{
    min-width:800px;
    min-height:700px;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: rgba(0,0,0,0.40);
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
    z-index: 10000;
  }

  .chose-response-wrap{
    min-width: 800px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 30px 40px 30px;
    border-radius: 10px;
    position: relative;
    box-sizing: border-box;
  }

  .chose-response-header{
    display: flex;
  }

  .chose-response-header .chose-response-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
    margin-left: 0px;
    flex: 1;
  }


  .chose-response-btn{
    width: 100px;
    height: 30px;
    border: none;
    outline: none;
    line-height: 30px;
    padding: 0 0;
    text-align: center;
  }

  .chose-response-btn-sure{
    background: #0087E7;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    margin-right: 20px;
  }

  .chose-response-btn-sure:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .chose-response-btn-sure:active{
    background-color: #0087E7;
  }

  .chose-response-btn-cancel{
    background: #D5D5D5;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    letter-spacing: 0;
    text-align: center;
    margin-right: 0px;
  }

  .chose-response-btn-cancel:hover{
    background-color: rgba(0,0,0,0.25);
    cursor: pointer;
  }

  .chose-response-btn-cancel:active{
    background-color: #D5D5D5;
  }

  .chose-response-wrap table{
    width: 100%;
    margin-top: 25px;
    border: none;
    border-collapse:collapse ;
  }

  .chose-response-wrap table th{
    height: 20px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #616366;
    letter-spacing: 0;
    line-height: 20px;
    padding: 0px 0px 30px 10px;
    border: none;
    text-align: left;
  }

  .chose-response-wrap table td{
    height: 50px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #0C101B;
    letter-spacing: 0;
    padding-left: 10px;
    border: none;
    border-bottom: solid 1px #E4E4E4;
    text-align: left;
  }

  .chose-response-wrap .chose-response-hover{
    cursor: pointer;
  }

  .chose-response-wrap .choseResponseUnSelected{
    background-image: url("../../assets/person_unselected.png");
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: 70px;
    background-size: 22px 22px;
  }

  .chose-response-wrap .choseResponseSelected{
    background-image: url("../../assets/person_selected.png");
  }

  .bottom-page-bar{
    position: absolute;
    left: 40px;
    bottom: 30px;
  }

</style>



