<template>
    <div id="page-control">
      <span class="page-control-page">/</span>
      <span class="page-control-page current">{{currentPage}}</span><span class="page-control-page total">/{{totalPage}}</span>
      <a class="page-control-page previous-page" v-bind:class="{disabled:!canPrevious}" href="javascript:void(0)" v-on:click.stop="clickedPrevious"></a>
      <a class="page-control-page next-page" v-bind:class="{disabled:!canNext}" href="javascript:void(0)" v-on:click.stop="clickedNext"></a>
    </div>
</template>

<script>
    export default {
        name: "PageControl",
        props:{
          currentPage:{
            type:Number,
            default:0,
          },
          totalPage:{
            type:Number,
            default:0,
          }
        },
        computed:{
          canPrevious:function () {
            return this.currentPage>1;
          },
          canNext:function () {
            return this.currentPage<this.totalPage;
          }
        },
      methods:{
        /**
         *
         * 
         *
         */
        clickedPrevious:function () {
          this.$emit('clickedPrevious');
          console.log('clickedPrevious');
        },

        /**
         *
         * 
         *
         */
        clickedNext:function () {
          this.$emit('clickedNext');
          console.log('clickedNext');
        }

      }
    }
</script>

<style scoped>

  .page-control-page{
    margin-top: 30px;
    display: inline-block;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #BBBBBB;
    letter-spacing: 0;
  }

  .current{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #0087E7;
    letter-spacing: 0;
    margin-right: 0px;
  }

  .total{
    margin-left: 0px;
  }

  .previous-page{
    margin-left: 34px;
    color: #0087E7;
  }

  .next-page{
    margin-left: 20px;
    color: #0087E7;
  }

  .disabled{
    pointer-events: none;
    color: #BBBBBB;
  }
</style>



