<template>
    <div class="toastWrap">
      <transition name="fade" v-on:after-leave="afterLeave">
        <div class="toastTitle" v-if="show">{{title}}</div>
      </transition>
    </div>
</template>

<script>
    import Vue from 'vue';
    import $ from 'jquery';

    export default {
      name: "Toast",
      props:['title','show'],
      methods:{
        afterLeave:function(el){
          this.$emit('leave');
        },
      },
      showToast:function (title) {
        $('.toastWrap').remove();
        let body=$('body');
        body.append('<div class="toastWrap"><Toast v-bind:title="title" v-bind:show="show" v-on:leave="toastLeave"></Toast></div>');
        let toast=new Vue({
          el:'.toastWrap',
          data:{
            title:title,
            show:true,
          },
          components:{
            Toast:this
          },
          methods:{
            toastLeave:function () {
              console.log('toastLeave');
              $('.toastWrap').remove();
            }
          },
        })

        setTimeout(function () {
          toast.show=false;
        },1000);

      }
    }

</script>

<style scoped>

  .toastWrap{
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    display: flex;
    justify-content: center;
    align-content: flex-start;
    z-index: 100000;
  }

  .toastTitle{
    margin-top: 10px;
    min-width: 400px;
    padding: 8px 20px;
    background-color: #eaa000;
    border-radius: 3px;
    text-align: center;
    color: white;
    font-size: 16px;
    line-height: 16px;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

</style>



