export function listOfPageWithPageSize(list,page,pageSize) {

  let totalPage=Math.ceil(list.length/pageSize);

  if (page>totalPage){

    return null;
  }
  else {

    let startIndex=(page-1)*pageSize;

    let endIndex=startIndex+pageSize;

    let newList=[];

    for (let i=startIndex;i<endIndex&&i<list.length;i++){

      newList.push(list[i]);
    }

    return newList;
  }

}



