<template>
  <div id="admin">
    <div class="adminContainer">

      <!---->
      <div class="nav">
        <div class="left">
          <img src="../../assets/logo.png" alt="">
        </div>
        <div class="right">
          <a href="javascript:void (0)" class="logout" v-on:click="clickLogout"></a>
          <div class="welcome">{{username}}</div>
        </div>
      </div>

      <!---->
      <div class="content">
        <!---->
        <div class="menuBar">
          <ul>
            <li class="menuItem" v-bind:class="{active:menuCurrentIndex==0}"  v-on:click="clickMenuItem(0)">
              <a href="javascript:void(0)">
                <img src="../../assets/sidebar_icon_template.png" alt="">
                <span></span>
              </a>
            </li>

            <li class="menuItem" v-bind:class="{active:menuCurrentIndex==1}" v-on:click="clickMenuItem(1)">
              <a href="javascript:void(0)">
                <img src="../../assets/sidebar_icon_team.png" alt="">
                <span></span>
              </a>
            </li>
          </ul>
        </div>

        <Logout v-bind:show="logoutAlertShow" v-on:logout="logout" v-on:cancelLogout="cancelLogout"/>

        <div class="frameContent">
          <keep-alive>
            <router-view  class="frameContentWrap" v-on:goToMenuItem="goToMenuItem"/>
          </keep-alive>
        </div>

      </div>
    </div>

  </div>
</template>

<script>

  import Logout from '../../components/logout/logout'

  import {clearUser,getStoreUser,userIsLogin} from "../../user/user";

  export default {
    name: 'Admin',
    data:function(){
      return {
        menuCurrentIndex:0,
        logoutAlertShow:false,
        user:null,
        templateRoute:null,
      }
    },
    computed:{
      username:function () {

        if (this.user){
          return this.user.username||this.user.nickname;
        }
        else {
          return '';
        }

      }
    },
    components: {
      Logout,
      Location
    },
    created:function(){
       this.checkUserIsLogin();
    },
    watch:{
      $route(to,from){

        this.checkUserIsLogin();

        if (to.name=='team'){

          this.templateRoute=from;

          console.log(from.params);
        }
      }
    },
    methods:{

      /**
       *
       * 
       *
       */
      checkUserIsLogin:function(){

        let isLogin= userIsLogin();

        if (!isLogin){
          window.location.href='login.html';
        }
        else{

          let user=getStoreUser();

          this.user=user;
        }
      },

      /**
       *
       * 
       *
       */
      clickLogout() {

        this.logoutAlertShow=true;

      },

      /**
       *
       * 
       *
       */
      logout:function(){

        this.logoutAlertShow=false;

        clearUser();

        window.location.href='login.html';

      },

      /**
       *
       * 
       *
       */
      cancelLogout:function(){

        this.logoutAlertShow=false;

      },

      /**
       *
       * menuItem
       *
       */
      goToMenuItem:function(index){

        if (this.menuCurrentIndex!=index){

          this.menuCurrentIndex=index;

        }

      },

      /**
       *
       * item
       *
       * @param menuItem
       */
      clickMenuItem(itemIndex) {

        if (this.menuCurrentIndex!=itemIndex){

          if (itemIndex==0){

            let name='templateList';
            let params={isBack:true};
            if (this.templateRoute){
              if (this.templateRoute.name){
                name=this.templateRoute.name;
              }
              if (this.templateRoute.params){
                params=Object.assign(params,this.templateRoute.params);
              }
            }
            this.$router.push({
              name:name,
              params:params
            });
          }
          else {
            this.$router.push({
              name:'team',
            });
          }



          this.menuCurrentIndex=itemIndex;

        };

      }

    }
  }
</script>

<style>
  html,body{
    padding: 0 0;
    margin: 0 0;
  }
</style>

<style scoped>

  #admin{
    width: 100%;
    min-height: 100%;
    min-width: 1200px;
    position: relative;
  }

  .adminContainer{
    width: 100%;
    position: relative;
  }

  .nav{
    width: 100%;
    height: 50px;
    background: #1E1E42;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow: hidden;
    margin-top: 0px;
  }



  .nav .left{
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .nav .right{
    height: 100%;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-start;
    margin-right: 40px;
  }

  .welcome{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: right;
    margin-right: 10px;
  }

  .logout{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFCF19;
    letter-spacing: 0;
    text-align: right;
    text-decoration: none;
  }

  .content{
    width: 100%;
    background-color: rgb(241,241,241);
    margin-top: 0px;
    margin-bottom: 0px;
    position: relative;
  }

  .menuBar{
    width: 240px;
    padding: 40px 0px;
    background: #16233A;
    box-sizing: border-box;
    position: absolute;
    top: 0px;
    bottom: 0px;
    z-index: 2;
  }

  .content:before {
    content: "";
    position: fixed;
    display: block;
    width: 240px;
    bottom: 0;
    top: 50px;
    left: 0px;
    background-color: #16233A;
    z-index: 1;
  }

  ul,li{
    list-style: none;
    padding: 0px 0px;
    margin-top: 0px;
    box-sizing: border-box;
  }

  .menuBar .menuItem{
    width: 100%;
    height: 40px;
    padding: 10px 0px;
    border-top: solid 1px rgba(255,255,255,0.2);
    box-sizing: border-box;
    overflow: hidden;
    z-index: 2;
  }

  .menuItem:last-child{
    border-bottom: solid 1px rgba(255,255,255,0.2);
  }

  .menuItem:hover{
    cursor: pointer;
  }

  .menuItem a{
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
  }

  .menuItem img{
    display: block;
    width:20px;
    height: 20px;
    margin-left: 74px;
    line-height: 20px;
  }

  .menuItem span{
    display: block;
    margin-left: 10px;
    min-width: 60px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
  }

  .menuBar .active{
    background-color:#0C101B;
    position: relative;
  }

  .active::after{
    content: '';
    position: absolute;
    width: 4px;
    height: 40px;
    z-index: 0;
    left: 0px;
    top: 0px;
    border-left: solid 4px  #0087E7;
  }

  .menuBar a{
    text-decoration: none;
  }

  .adminContainer:before{
    content: "";
    position: fixed;
    display: block;
    bottom: 0;
    top: 50px;
    left: 240px;
    right: 0px;
    background-color: rgb(241,241,241);
    z-index: 0;
  }

  .frameContent{
    margin-left: 240px;
    margin-right: 0px;
    position: relative;
  }



  .frameContentWrap{
    z-index: 2;
  }

</style>



