<template>
  <div id="team">
    <Location v-bind:locations="locations"/>
    <div v-if="show"  class="team-wrap">
      <div class="team-list">
        <table style="width: 100%">
          <tr>
            <th v-for="title in titles">{{title}}</th>
            <th></th>
          </tr>
          <tr v-for="(person,index) in currentPersonArr">
            <td style="width: 160px">{{person.username||person.nickname}}</td>
            <td style="width: 220px">{{person.phoneno}}</td>
            <td style="width: 160px">{{person.departmentName}}</td>
            <td style="width: 180px">{{person.position}}</td>
            <td style="width: 80px">{{person.status==0?'':''}}</td>
            <td></td>
          </tr>
        </table>
        <PageControl class="bottom-page-bar" v-bind:current-page="currentPage" v-bind:total-page="totalPage"  v-on:clickedPrevious="clickedPrevious" v-on:clickedNext="clickedNext"/>
      </div>
    </div>

  </div>
</template>

<script>

  import Location from '../../components/location/Location'

  import PageControl from '../../components/pageControl/PageControl'

    import {loadTeamMemberList,listOfPageWithPageSize} from "./teamManager";

    export default {
      name: "Team",
      data:function () {
        return {
          locations:[''],
          show:true,
          titles:['','','','',''],
          personArr:null,
          currentPersonArr:null,
          currentPage:0,
          totalPage:0,
          pageSize:10,
        }
      },
      components:{
        Location,
        PageControl
      },
      mounted() {
        this.getTeamMemberList();
      },
      beforeRouteEnter:function (to,from,next) {
        next(vm => {
          //  `vm` 
          vm.$emit('goToMenuItem',1);

          console.log('beforeRouteEnter');
        })

      },
      methods:{
        /**
         *  
         *
         */
        clickLocation:function(index){

        },

        /**
         * 
         *
         */
        clickedPrevious:function(){

          this.currentPage=Math.max(this.currentPage-1,1);

          this.currentPersonArr=listOfPageWithPageSize(this.templateList,this.currentPage,this.pageSize);

        },

        /**
         * 
         *
         */
        clickedNext:function(){

          this.currentPage=Math.min(this.currentPage+1,this.totalPage);

          this.currentPersonArr=listOfPageWithPageSize(this.templateList,this.currentPage,this.pageSize);
        },

        /**
         * 
         *
         */
        getTeamMemberList:function(){

          let page=Math.max(this.currentPage,1);

          let pageSize=this.pageSize;

          const that=this;

          loadTeamMemberList(function (isSuccess,list,baseResponse) {

            if (isSuccess){

              that.personArr=list;

              if (that.personArr.length){

                that.currentPage=1;

              }else {

                that.currentPage=0;
              }

              that.totalPage=Math.ceil(that.personArr.length/pageSize);

              that.show=true;

              that.currentPersonArr=listOfPageWithPageSize(that.personArr,1,pageSize);

            }
            else{

              let title = '';

              if (baseResponse && baseResponse.msg) {

                title = baseResponse.msg;
              }

              Toast.showToast(title);

            }
          });
        },
      }

    }
</script>

<style scoped>

  #team{
    width: 100%;
    height: 100%;
  }

  .team-wrap{
    width: 100%;
    min-height: 500px;
    display: flex;
    min-width: 1000px;
    margin-top: 0px;
  }

  .team-list{
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 15px 20px 50px;
    position: relative;
  }


  .team-list table{
    margin-top: 25px;
    border: none;
    border-collapse:collapse ;
  }

  .team-list table th{
    height: 20px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #616366;
    letter-spacing: 0;
    line-height: 20px;
    padding: 0px 0px 30px 10px;
    border: none;
    text-align: left;
  }

  .team-list table td{
    height: 50px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #0C101B;
    letter-spacing: 0;
    padding-left: 10px;
    border: none;
    border-bottom: solid 1px #E4E4E4;
  }

  .bottom-page-bar{
    position: absolute;
    left: 20px;
    bottom: 50px;
  }

</style>



