<template>
  <div class="flow-list-wrap">
    <div class="flow-list">
      <p class="flow-list-title"></p>
      <p class="template-title">{{template?template.name:' '}}</p>
      <div class="operate-tips">* </div>
      <div class="mtable">
        <div class="mtr">
          <div class="mth mtd"></div>
          <div class="mth mtd"></div>
          <div class="mth mtd"></div>
        </div>
        <div class="mtr" v-for="(item,index) in flowList"
             v-bind:data-index="index"
             v-on:mouseenter="mouseenter(index)"
             v-on:mouseleave="mouseleave(index)">
          <div class="mtd">{{index+1}}</div>
          <div class="mtd">{{item.name}}</div>
          <div class="mtd">
            <div class="flow-operate-wrap">
              <a href="javascript:void(0)" v-on:click="clickedOperate(index,$event)" data-oIndex=0></a>
              <a href="javascript:void(0)" v-on:click="clickedOperate(index,$event)" data-oIndex=1></a>
              <a href="javascript:void(0)" v-show="moveIndex==index" class="move" v-on:click="clickedOperate(index,$event)" data-oIndex=2></a>
            </div>
          </div>
        </div>
      </div>
      <button class="addFlow" v-on:click="clickAddFlow()">+</button>
    </div>
  </div>
</template>

<script>

  import {loadTemplateDetailOfId,deleteFlowOfId,modifyFlowOrderOfId} from "../templateList/templateManager";

  import {FlowOperateType} from "../flow/FlowOperateType";

  import {ProcessDetailModel} from "../templateList/templateManager";

  import $ from 'jquery';

  import '../../lib/jquery.easyui.min';

  import Toast from '../../components/toast/Toast';

  export default {
    name: "templateEdit",
    data:function () {
      return {
        show:false,
        flowList:[],
        template:null,
        moveIndex:-1,
      }
    },
    activated:function(){
      this.show=true;
      let params=this.$route.params;
      if (params&&params.isBack){
        return;
      }else {
        this.resetData();
      }
      if (params&&params.template){
        this.template=params.template;
        this.getTemplateDetail();
      }
      else{
        this.$router.push({
          name:'templateList'
        })
      }
    },
    beforeRouteLeave:function(to, from, next){

      if (to.name&&(to.name=='addFlow'||to.name=='team')) {

      }
      else {
        this.resetData();
      }
      next();
    },
    methods:{

      resetData:function(){
        this.show=false;
        this.modelId=0;
        this.flowList=[];
      },

      /**
       * table
       *
       */
      configureDragable:function(){

        const that=this;

        $('.mtr').draggable({
          proxy: 'clone',
          handle: '.move',
          revert: true,
          axis: 'v',
        }).droppable({
          onDragOver: function(e, source) {
            $(source).draggable('proxy').css('box-shadow', '0 0 5px #0087E7');
            $(source).draggable('proxy').children('.mtd').css('border-bottom', '1px solid #E4E4E4');
            $(source).draggable('proxy').children('.mtd').css('border-top', '1px solid #E4E4E4');
          },
          onDragLeave: function(e, source) {

          },
          onDrop: function(e, source) {

            let from=parseInt(source.dataset.index);

            let to= parseInt(this.dataset.index);

            that.modifyFlowOrder(from,to);

            return false;
          }
        });
      },

      /**
       * 
       *
       */
      getTemplateDetail:function(){

        const that=this;

        loadTemplateDetailOfId(this.template.modelId,function (isSuccess,list,baseResponse) {

          if (isSuccess){

            that.flowList=list;

            that.$nextTick();

            setTimeout(function () {
              that.configureDragable();
            },1);


          }
          else{

          }

        });
      },

      /**
       * 
       *
       * @param index flowindex
       */
      mouseenter:function(index){

        this.moveIndex=index;

      },

      /**
       * 
       *
       * @param index flowindex
       */
      mouseleave:function(index){
        this.moveIndex=-1;
      },

      /**
       * 
       *
       * @param index
       */
      clickAddFlow:function(){

        const that=this;

        let newFlow=new Object();

        newFlow.id=null;

        newFlow.flowName=null;

        newFlow.level=that.flowList.length+1;

        newFlow.modelId=this.template.modelId;

        newFlow.companyId=this.template.companyId;

        newFlow.departmentId=this.template.departmentId;

        newFlow.type=this.template.type;

        newFlow.processList=null;

        let operateType=FlowOperateType.editTemplateAddFlow;

        let operateFlowCompletion=function (done,flow) {

          if (done){
            //flow
            let flowModel={
              name:flow.flowName,
              id:flow.id,
              modelId:flow.modelId,
              departmentId:flow.departmentId,
              companyId:flow.companyId,
              level:flow.level,
              type:flow.type,
              programProcesses:null
            }

            let programProcesses=[];

            for (let i=0;i<flow.processList.length;i++){

              let process=flow.processList[i];

              let newProcess=new ProcessDetailModel();

              newProcess.name=process.name;

              newProcess.defaultChargePersion=process.response.userId;

              newProcess.userName=process.response.userName;

              newProcess.defaultExamPersion=process.review.userId;

              newProcess.dEName=process.review.userName;

              programProcesses.push(newProcess);

            }

            flowModel.programProcesses=programProcesses;

            that.flowList.push(flowModel);

            that.getTemplateDetail();
          }

          that.$router.push({
            name:'editTemplate',
            params:{
              isBack:true
            }
          })

        }

        let templateType={
          id:this.template.departmentId,
          type:this.template.type,
        }

        if (templateType==1){
          templateType.name='';
        }
        else {
          templateType.name='';
        }

        this.$router.push({
          name:'addFlow',
          params:{
            operateType:operateType,
            operateFlowCompletion:operateFlowCompletion,
            flow:newFlow,
            templateType:templateType
          }
        })
      },

      /**
       * item
       * @param index index
       * @param event 
       */
      clickedOperate:function (index,event) {

        let oIndex=event.target.dataset.oindex;

        oIndex=parseInt(oIndex);

        switch (oIndex) {
          case 0:
            this.editFlow(index);
            break;

          case 1:
            this.deleteFlow(index);
            break;

          default:
            break;
        }

      },

      /**
       * 
       *
       * @param index
       */
      editFlow:function (index) {

        let flow=this.flowList[index];

        //flow
        let newFlow=new Object();

        newFlow.flowName=flow.name;

        newFlow.id=flow.id;

        newFlow.level=flow.level;

        newFlow.modelId=flow.modelId;

        newFlow.companyId=flow.companyId;

        newFlow.departmentId=flow.departmentId;

        newFlow.type=flow.type;

        let processList=[];

        for (let i=0;i<flow.programProcesses.length;i++){

          let process=flow.programProcesses[i];

          let newProcess={
            name:process.name,
            defaultTimeSpan:process.defaultTimeSpan,
            response:null,
            review:null
          };

          if (process.defaultChargePersion){

            newProcess.response={
              userId:process.defaultChargePersion,
              userName:process.userName
            };
          }

          if (process.defaultExamPersion){

            newProcess.review={
              userId:process.defaultExamPersion,
              userName:process.dEName
            };
          }

          processList.push(newProcess);

        }

        newFlow.processList=processList;

        let operateType=FlowOperateType.editTemplateEditFlow;

        const that=this;

        let operateFlowCompletion=function (done,mFlow) {

          if (done){

            //flow
            let flowModel={
              name:mFlow.flowName,
              id:mFlow.id,
              modelId:mFlow.modelId,
              departmentId:mFlow.departmentId,
              companyId:mFlow.companyId,
              level:mFlow.level,
              type:mFlow.type,
              programProcesses:null
            }

            let programProcesses=[];

            for (let i=0;i<mFlow.processList.length;i++){

              let process=mFlow.processList[i];

              let newProcess=new ProcessDetailModel();

              newProcess.name=process.name;

              newProcess.defaultChargePersion=process.response.userId;

              newProcess.userName=process.response.userName;

              newProcess.defaultExamPersion=process.review.userId;

              newProcess.dEName=process.review.userName;

              programProcesses.push(newProcess);

              that.getTemplateDetail();

            }

            flowModel.programProcesses=programProcesses;

            that.flowList.splice(index,1,flowModel);

          }

          that.$router.push({
            name:'editTemplate',
            params:{
              isBack:true
            }
          })

        }

        let templateType={
          id:this.template.departmentId,
          type:this.template.type,
        }

        if (templateType==1){
          templateType.name='';
        }
        else {
          templateType.name='';
        }

        this.$router.push({
          name:'addFlow',
          params:{
            operateType:operateType,
            operateFlowCompletion:operateFlowCompletion,
            flow:newFlow,
            templateType:templateType
          }
        })

      },

      /**
       * 
       *
       */
      deleteFlow:function (index) {

        let flow=this.flowList[index];

        let sure=confirm(''+flow.name);

        if (sure){

          const that=this;

          deleteFlowOfId(flow.id,function (isSuccess,flowList,baseResponse) {

            if (isSuccess){

              that.flowList=flowList;
            }
            else {

              let title = '';

              if (baseResponse && baseResponse.msg) {

                title = baseResponse.msg;
              }

              Toast.showToast(title);
            }

          });

        }

      },


      /**
       * 
       * @param from
       * @param to
       */
      modifyFlowOrder:function(from,to){

        let fItem=this.flowList[from];

        let tItem=this.flowList[to];

        let  originalList=[];

        originalList=originalList.concat(this.flowList);

        this.flowList.splice(from,1);

        let tIndex=this.flowList.indexOf(tItem);

        this.flowList.splice(tIndex+1,0,fItem);


        const that=this;

        modifyFlowOrderOfId(fItem.id,to+1,function (isSuccess,flowList,baseResponse) {

          if (isSuccess){

            that.flowList=flowList;
          }
          else {

            that.flowList=originalList;

            let title = '';

            if (baseResponse && baseResponse.msg) {

              title = baseResponse.msg;
            }

            Toast.showToast(title);

          }

        });


      },
    },
  }
</script>

<style scoped>
  .flow-list-wrap{
    width: 100%;
    display: flex;
    min-width: 1000px;
    margin-top: 0px;
  }

  .flow-list-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
  }

  .addFlow{
    width: 120px;
    height: 28px;
    background: #0087E7;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    padding: 0px;
    border: 1px solid transparent;
    outline: none;
    margin-top: 30px;
  }

  .addFlow:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .addFlow:active{
    background: #0087E7;
  }

  .operate-tips{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #A9A9A9;
    letter-spacing: 0;
    margin-top: 5px;
    margin-bottom: 10px;
  }

  .flow-list{
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 15px 20px 30px;
  }


  .flow-list-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
  }

  .flow-list .mtable{
    overflow: hidden;
    position: relative;
  }

  .flow-list .mtd{
    border:1px solid #E4E4E4;
  }

  .mtr:nth-of-type(n+2)>.mtd{
    border-top: none;
  }


  .flow-list .mtable .mtr{
    width: 100%;
    text-align: center;
    display: flex;
    box-sizing: border-box;
    background-color: white;
  }

  .flow-list .mtable .mtr:nth-child(1){
    height: 40px;
    line-height: 40px;
  }

  .flow-list .mtable .mtr:nth-child(n+2){
    height: 40px;
  }


  .flow-list .mtable .mth{
    text-align: center;
  }

  .flow-list .mtable .mth:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
  }

  .flow-list .mtable .mth{
    flex: 1;
    background: #F4F4F4;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #676767;
    text-align: center;
  }

  .flow-list .mtable .mtd{
    text-align: center;
  }

  .flow-list .mtable .mtd:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
    border-left: none;
  }


  .flow-list .mtable .mtd{
    flex: 1;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #0C101B;
    letter-spacing: 0;
    line-height: 40px;
  }

  .flow-list .mtable .mtd:nth-child(n+2){
    flex: 3;
  }

  .flow-list .mtable .mtd:nth-child(n+3){
    flex: 2;
  }


  .flow-list .flow-operate-wrap{
    display: flex;
    align-content: center;
    align-items: center;

  }

  .flow-operate-wrap a{
    color: #0087E7;
    text-decoration: none;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    letter-spacing: 0;
    display: inline-block;
  }

  .flow-operate-wrap a:nth-child(1){
    padding: 0px 0px;
  }

  .flow-operate-wrap a:nth-child(n+2){
    padding-left: 30px;
  }

  .flow-operate-wrap .move{
    display: inline-block;
    width: 50px;
    height: 24px;
    background: url("../../assets/flow_move.png") no-repeat ;
    background-size: 24px 24px;
    background-position-x: center;
    box-sizing: border-box;
    margin-left: 12px;
  }

</style>



