<template>
  <div class="add-template-wrap">
    <div class="add-template-content" v-bind:class="{forbidEdit:forbidEdit}">
      <div class="add-template-title">{{functionTitle}}</div>
      <div class="template-title"></div>
      <input class="template-title-input" type="text" placeholder="" v-model="templateTitle">
      <div class="template-type-title"></div>
      <div class="template-type-value" v-if="!templateTypeChoseEnable">{{templateType?templateType.name:''}}</div>
      <div class="template-type-select-wrap" v-else="templateTypeChoseEnable" >
        <button class="btn-select-type" v-bind:class="{btnSelectTypeActive:selectActive,btnPlaceholder:!templateType}" v-on:click="clickedSelectType">{{templateType?templateType.name:''}}</button>
        <ul class="type-menu" v-bind:class="{typeMenuActive:selectActive}">
          <li v-for="(option,index) in templateTypeList" v-on:click="clickedType(index)" class="type-menu-option" ><a v-bind:class="{optionSelected:templateType==option}" href="javascript:void(0)">{{option.name}}</a></li>
        </ul>
      </div>
      <div class="template-remarks"></div>
      <textarea class="template-remarks-textarea" name="" id="" cols="30" rows="10" placeholder="" v-model="templateRemarks"></textarea>

      <template v-if="flowList.length">
        <div class="template-hasAdd-title"></div>
        <table>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(flow,index) in flowList">
            <td>{{index+1}}</td>
            <td>{{flow.flowName}}</td>
            <td>
              <div class="add-template-flow-operate-wrap">
                <a href="javascript:void(0)" v-on:click="clickedOperate(index,$event)" data-oIndex=0></a>
                <a href="javascript:void(0)" v-on:click="clickedOperate(index,$event)" data-oIndex=1></a>
              </div>
            </td>
          </tr>
        </table>

      </template>

      <button class='btn-add-flow' v-on:click="clickedAddFlow">+ </button>
    </div>

    <div class="add-template-btn-wrap">
      <button class="operate-button btn-save-flow" v-bind:disabled="forbidEdit" v-on:click="clickSaveTemplate">{{saveButtonTitle}}</button>
      <button class="operate-button btn-cancel-flow" v-on:click="clickCancelTemplate"></button>
    </div>

    <div v-if="selectActive" class='selectMask' v-on:click="clickedCancelSelectType"></div>

  </div>
</template>

<script>

    import {FlowOperateType} from "../flow/FlowOperateType";

    import {createTemplate,loadDepartments,loadTemplateDetailOfId} from "../templateList/templateManager";

    import Toast from '../../components/toast/Toast';

    import {getStoreUser} from "../../user/user";

    export default {
      name: "addTemplate",
      data:function () {
        return {
          functionTitle:'',
          copyTemplate:null,
          templateTypeChoseEnable:true,
          flowList:[],
          templateTitle:null,
          templateType:null,
          templateRemarks:null,
          selectActive:false,
          templateTypeList:[{
            name:'',
            id:null,
            type:1
          },{
            name:'',
            id:null,
            type:2
          }],
          templateTypeListLoadSuccess:true,
          addTemplateCompletion:null,
          saveButtonTitle:'',
          forbidEdit:false,
        }
      },
      activated:function(){

        let params=this.$route.params;

        if (params){

          if (!params.isBack){

            this.resetData();
          }

          if (params.addTemplateCompletion){

            this.addTemplateCompletion=params.addTemplateCompletion;
          }

          if (params.copyTemplate){

            let  copyTemplate=params.copyTemplate;

            this.functionTitle='';

            this.templateTitle=copyTemplate.name;


            if (copyTemplate.type==1){

              this.templateType={
                name:'',
                id:null,
                type:1
              };

            }
            else{

              this.templateType={
                name:'',
                id:copyTemplate.departmentId,
                type:2
              };

            }

            this.copyTemplate=copyTemplate;

            this.templateRemarks=copyTemplate.description;

            this.getTemplateDetail();

          }

        }

        if (this.copyTemplate){

          this.templateTypeChoseEnable=false;
        }
        else{

          this.templateTypeChoseEnable=true;
        }

      },
      beforeRouteLeave:function(to, from, next){

        if (to.name&&(to.name=='addFlow'||to.name=='team')) {

        }
        else {
          this.resetData();
        }
        next();
      },
      methods:{

        resetData:function(){
          this.functionTitle='';
          this.copyTemplate=null;
          this.flowList=[];
          this.templateTitle=null;
          this.templateType=null;
          this.templateRemarks=null;
          this.selectActive=false;
          this.templateTypeList=[{
            name:'',
            id:null,
            type:1
          },{
            name:'',
            id:null,
            type:2
          }];
          this.templateTypeListLoadSuccess=true;
          this.addTemplateCompletion=null;
          this.forbidEdit=false;
          this.saveButtonTitle=''

        },

        /**
         * 
         *
         */
        getTemplateDetail:function(){

          const that=this;

          loadTemplateDetailOfId(this.copyTemplate.modelId,function (isSuccess,list,baseResponse) {

            if (isSuccess){

              that.handleTemplateDetailFlowList(list);
            }
            else{

              let title = '';

              if (baseResponse && baseResponse.msg) {

                title = baseResponse.msg;
              }

              Toast.showToast(title);

            }

          });
        },

        /**
         *
         * 
         *
         */
        handleTemplateDetailFlowList:function(list){

          let flowList=[];

          for (let i=0;i<list.length;i++){

            let flow=list[i];

            //flow
            let newFlow=new Object();

            newFlow.flowName=flow.name;

            newFlow.id=flow.id;

            newFlow.level=flow.level;

            newFlow.modelId=flow.modelId;

            newFlow.companyId=flow.companyId;

            newFlow.departmentId=flow.departmentId;

            newFlow.type=flow.type;

            let processList=[];

            for (let i=0;i<flow.programProcesses.length;i++){

              let process=flow.programProcesses[i];

              let newProcess={
                name:process.name,
                defaultTimeSpan:process.defaultTimeSpan,
                response:null,
                review:null
              };

              if (process.defaultChargePersion){

                newProcess.response={
                  userId:process.defaultChargePersion,
                  userName:process.userName
                };
              }

              if (process.defaultExamPersion){

                newProcess.review={
                  userId:process.defaultExamPersion,
                  userName:process.dEName
                };
              }

              processList.push(newProcess);

            }

            newFlow.processList=processList;

            flowList.push(newFlow);

          }

          this.flowList=flowList;

        },

        /**
         *
         * 
         *
         */
        getDepartments:function(callBack){

          const that=this;

          loadDepartments(function (isSuccess,list,baseResponse) {

            if (that.templateTypeListLoadSuccess){

              return;
            }

            if (isSuccess){

              for (let i=0;i<list.length;i++){

                let department=list[i];

                let templateType=new Object();

                templateType.id=department.id;

                templateType.name=department.departmentName;

                templateType.type=2;

                that.templateTypeList.push(templateType);

                that.templateTypeListLoadSuccess=true;

              }

            }

            if (callBack){
              callBack(isSuccess,list,baseResponse);
            }

          });

        },

        /**
         *
         * 
         *
         */
        clickedSelectType:function(){

          const that=this;

          let fn=function () {

            that.selectActive=!that.selectActive;

          }

          if (!that.templateTypeListLoadSuccess){

            that.getDepartments(function (isSuccess,list,baseResponse) {

              if (isSuccess){

                fn()
              }

            })
          }
          else{

            fn();
          }

        },

        /**
         *
         * 
         *
         */
        clickedType:function(index){

          let templateType=this.templateTypeList[index];

          console.log(templateType);

          if (this.templateType&&this.flowList&&this.flowList.length){

            if (this.templateType&&this.templateType.type==templateType.type){

              this.selectActive=false;

              return;

            }
            let sure=confirm('');

            if (sure){

              this.flowList=[];

              this.templateType=templateType;

              this.selectActive=false;

            }

          }
          else {

            this.templateType=templateType;

            this.selectActive=false;
          }

        },

        /**
         *
         * 
         *
         */
        clickedCancelSelectType:function(){

          this.selectActive=false;
        },

        /**
         *
         * 
         *
         */
        clickedAddFlow:function () {

          if (this.templateType==null){
            Toast.showToast('');
            return;
          }

          const that=this;

          let operateType=FlowOperateType.newTemplateAddFlow;

          let operateFlowCompletion=function (done,flow) {

            if (done){
              that.flowList.push(flow);
            }

            that.$router.push({
              name:'addTemplate',
              params:{
                isBack:true,
              }
            })

          }

          let templateType=this.templateType;

          if (templateType.type==2){

            templateType.id=getStoreUser().departmentId;
          }

          this.$router.push({
            name:'addFlow',
            params:{
              operateType:operateType,
              operateFlowCompletion:operateFlowCompletion,
              templateType:Object.assign({},this.templateType)
            }
          })

        },

        /**
         * item
         * @param index flow index
         * @param event 
         */
        clickedOperate:function (index,event) {

          let oIndex=parseInt(event.target.dataset.oindex);

          switch (oIndex) {
            case 0:
              this.editFlow(index);
              break;

            case 1:
              this.deleteFlow(index);
              break;
            default:
              break;
          }

        },



        /**
         * flow
         *
         * @param index
         */
        editFlow:function (index) {

          const that=this;

          let operateType=FlowOperateType.newTemplateAddFlow;

          let editFlow=this.flowList[index];

          let operateFlowCompletion=function (done,flow) {

            if (done){
              that.flowList.splice(index,1,flow);
            }

            that.$router.push({
              name:'addTemplate',
              params:{
                isBack:true,
              }
            })

          }

          let templateType=this.templateType;

          if (templateType.type==2){

            templateType.id=getStoreUser().departmentId;
          }

          this.$router.push({
            name:'addFlow',
            params:{
              operateType:operateType,
              operateFlowCompletion:operateFlowCompletion,
              flow:editFlow,
              templateType:templateType
            }
          })

        },

        /**
         * flow
         *
         */
        deleteFlow:function (index) {

          this.flowList.splice(index,1);

        },

        /**
         *
         * 
         *
         */
        clickSaveTemplate:function () {

          if (this.forbidEdit){
            return;
          }

          const that=this;

          let canSave=this.verifyCanSaveTemplate();

          if (canSave.result){

            this.forbidEdit=true;

            this.saveButtonTitle='';

            let user=getStoreUser();

            let template={
              name:this.templateTitle,
              type:this.templateType.type,
              description:this.templateRemarks,
              flowList:this.flowList
            }

            let departmentId=user.departmentId;

            if (departmentId&&this.templateType.id){

              template.departmentId=departmentId;
            }

            createTemplate(template,function (isSuccess,list,baseResponse) {

              that.forbidEdit=false;

              that.saveButtonTitle='';

              if (isSuccess){

                if (that.addTemplateCompletion){

                  that.addTemplateCompletion(true);

                  that.resetData();
                }

              }
              else {

                let title = '';

                if (baseResponse && baseResponse.msg) {

                  title = baseResponse.msg;
                }

                Toast.showToast(title);

              }

            });

          }
          else{
            Toast.showToast(canSave.message);
          }

        },


        /**
         *
         * 
         *
         */
        clickCancelTemplate:function () {

          //  
          if (this.forbidEdit){

            this.forbidEdit=false;

            this.saveButtonTitle='';

            return;

          }

          // 
          if (this.addTemplateCompletion){

            this.addTemplateCompletion(false);
          }

          this.resetData();

        },

        /**
         * 
         *
         * @returns {{result: boolean, message: *}}
         */
        verifyCanSaveTemplate:function() {

          let result=true;

          let message=null;

          if (!this.templateTitle||!this.templateTitle.length){
            result=false;
            message='';
          }
          else if (this.copyTemplate&&this.copyTemplate.name==this.templateTitle){
            result=false;
            message='';
          }
          else if (!this.templateType)
          {
            result=false;
            message='';
          }
          else if (!this.flowList||!this.flowList.length){
            result=false;
            message='';
          }

          return {
            result:result,
            message:message
          }
        }

      }
    }
</script>

<style scoped>

  body{
    width: 100%;
    height: 100%;
  }

  .add-template-wrap{
    width: 100%;
    display: flex;
    min-width: 1000px;
    margin-top: 0px;
    flex-direction: column;
  }

  .add-template-content{
    min-height: 412px;
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 15px 20px 50px;
  }

  .forbidEdit{
    pointer-events: none;
  }

  .add-template-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
  }

  .template-title{
    margin-top: 20px;
  }

  .template-set-title{
    margin-top: 10px;
  }

  .template-title,.template-type-title,.template-remarks,.template-hasAdd-title{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    letter-spacing: 0;
    line-height: 18px;
    margin-top: 15px;
  }

  .template-type-title,.template-remarks{
    margin-top: 15px;
    margin-bottom: 5px;
  }

  .template-title-input{
    width: 520px;
    margin-top: 5px;
    box-sizing: border-box;
  }

  .template-title-input{
    height: 40px;
    background: #F4F4F4;
    border: none;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    padding: 0px 10px;
  }

  .template-type-value{
    width: 520px;
    margin-top: 5px;
    box-sizing: border-box;
    height: 40px;
    background: #F4F4F4;
    border: none;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    padding: 0px 10px;
    line-height: 40px;
  }

  .template-type-select-wrap{
    width: 520px;
    margin-top: 5px;
    box-sizing: border-box;
    height: 40px;
    background: #F4F4F4;
    position: relative;
    z-index: 100;
  }

  .selectMask{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 0px 0px;
    display: block;
    z-index: 99;
  }


  .btn-select-type{
    width: 100%;
    height: 100%;
    text-align: left;
    padding: 0px 10px;
    background-color: #F4F4F4;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: black;
    letter-spacing: 0;
    border: none;
    outline: none;
    background-image: url("../../assets/btn-triangle.png");
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: 495px;
    margin-bottom: 0px;
  }

  .btnPlaceholder{
    color: #BBBBBB;
  }


  .btnSelectTypeActive{
    border: 2px solid rgba(0,0,0,0.15);
    border-bottom: none;
  }

  .type-menu{
    width: 100%;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 40px;
    margin-top: 0px;
    background-color: #F4F4F4;
    padding: 0px 0px;
    visibility: hidden;
    display: block;
    box-sizing: border-box;
  }

  .typeMenuActive{
    padding: 0px 0px;
    border: 2px solid rgba(0,0,0,0.10);
    border-top: none;
    visibility: visible;
  }

  .type-menu-option{
    height: 40px;
    list-style:none;
    padding: 0px 0px;
    border-top: solid 1px rgba(0,0,0,0.15);
  }

  .optionSelected{
    background-color: rgba(0,0,0,0.10);
  }

  .type-menu-option a{
    list-style:none;
    display: block;
    padding: 0px 10px;
    width: 100%;
    height: 100%;
    text-decoration: none;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    line-height: 40px;
    color: black;
    display: block;
    box-sizing: border-box;
  }

  .type-menu-option a:hover{
    background-color: rgba(0,0,0,0.10);
  }

  .template-remarks-textarea{
    width: 520px;
    height: 100px;
    padding: 10px 10px;
    line-height: 20px;
    border: none;
    background-color: #F4F4F4;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    line-height: 20px;
    color: black;
    letter-spacing: 0;
    resize: none;
    box-sizing: border-box;
  }


  .add-template-content table{
    width: 520px;
    border-collapse:collapse;
    margin-top: 10px;
    margin-bottom: 15px;
  }


  .add-template-content table,.add-template-content td,.add-template-content th{
    border:1px solid #E4E4E4;
  }



  .add-template-content table tr{
    text-align: center;
    height: 40px;
    height: 40px;
    box-sizing: border-box;
  }

  .add-template-content table th{
    text-align: center;
    height: 39px;
    height: 39px;
    flex: 1;
    background: #F4F4F4;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #676767;
    text-align: center;
  }

  .add-template-content table th:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
  }


  .add-template-content table td{
    text-align: center;
    flex: 1;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #0C101B;
    letter-spacing: 0;
    line-height: 40px;
  }

  .add-template-content table td:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
  }

  .add-template-content .add-template-flow-operate-wrap{
    display: flex;
  }

  .add-template-flow-operate-wrap a{
    color: #0087E7;
    text-decoration: none;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    letter-spacing: 0;
  }

  .add-template-flow-operate-wrap a{
    color: #0087E7;
    text-decoration: none;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    letter-spacing: 0;
  }

  .add-template-flow-operate-wrap a:nth-child(1){
    padding: 0px 0px;
  }

  .add-template-flow-operate-wrap a:nth-child(n+2){
    padding-left: 30px;
  }


  .btn-add-flow{
    width: 120px;
    height: 28px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
    outline: none;
    border: none;
    background-color:#0087E7;
    display: block;
  }

  .btn-add-flow:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .btn-add-flow:active{
    background-color:#0087E7;
  }

  .add-template-btn-wrap{
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    margin-top: 20px;
  }

  .add-template-btn-wrap .operate-button{
    width: 160px;
    height: 40px;
  }

  .add-template-btn-wrap .btn-save-flow{
    background: #0087E7;
    border: none;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    text-align: center;
    outline: none;
  }

  .add-template-btn-wrap .btn-save-flow:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .add-template-btn-wrap .btn-save-flow:active{
    background-color: #0087E7;
  }

  .add-template-btn-wrap .btn-cancel-flow{
    background: #D5D5D5;
    border: none;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    text-align: center;
    outline: none;
    margin-left: 20px;
  }

  .add-template-btn-wrap .btn-cancel-flow:hover{
    background-color: rgba(0,0,0,0.25);
    cursor: pointer;
  }

  .add-template-btn-wrap .btn-cancel-flow:active{
    background-color: #D5D5D5;
  }
</style>



