<template>
  <div class="mask" v-if="show">
    <div class="alertBack">
      <div class="alertTitle"></div>
      <div class="alertMessage"></div>
      <div class="buttonView">
        <button type="button" class="sureButton customButton" v-on:click="logout"></button>
        <button type="button" class="cancelButton customButton" v-on:click="cancelLogout"></button>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "logout",
        props:{
          show:{
            type:Boolean,
            default: false
          }
        },
      methods:{
        logout:function () {
          this.$emit('logout');
        },
        cancelLogout:function () {
          this.$emit('cancelLogout');
        },
      }
    }
</script>

<style scoped>
  .mask{
    width: 100%;
    height: 100%;
    min-width: 1200px;
    min-height: 800px;
    background-color: rgba(0,0,0,0.40);
    position: fixed;
    z-index: 1000;
    top:0px;
    left: 0px;
    display: flex;
    justify-items: center;
    justify-content: center;
    align-items: center;
    align-content: center;
  }

  .mask .alertBack{
    width: 300px;
    height: 200px;
    background: #FFFFFF;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.20);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
  }

  .alertBack .alertTitle{
    margin-top: 30px;
    min-height: 20px;
    line-height: 20px;
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #0C101B;
    letter-spacing: 0;
    text-align: center;
  }

  .alertBack .alertMessage{
    margin-top: 20px;
    min-height: 20px;
    line-height: 20px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #0C101B;
    letter-spacing: 0;
    text-align: center;
  }

  .alertBack .buttonView{
    margin-top: 40px;
    width: 100%;
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
  }

  .sureButton{
    width:100px ;
    height: 30px;
    background: #0087E7;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    margin-left: 20px;
  }

  .sureButton:hover{
    background-color: #006eff;
  }

  .sureButton:active{
    background: #0087E7;
  }

  .cancelButton{
    width:100px ;
    line-height: 30px;
    background: #D5D5D5;
    border-radius: 6px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #676767;
    letter-spacing: 0;
    text-align: center;
    margin-right: 20px;
  }

  .cancelButton:hover{
    background-color: rgba(0,0,0,0.40);
  }

  .cancelButton:active{
    background: rgba(0,0,0,0.30);
  }


  .customButton{
    padding: 0px;
    border: 1px solid transparent;
    outline: none;
  }
</style>



