<template>
  <div id="templateContent">
    <div v-if="show" class="template-list-wrap">
      <div class="template-list">
        <p class="template-list-title"></p>
        <button class="btn-add-template" v-on:click="clickedAddTemplate">+ </button><input class="input-search-template" v-on:keyup.enter="searchTemplate" v-model="inputKeywords" placeholder="">
        <table>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>

          <tr v-for="(item,index) in currentTemplateList">
            <td>{{index+1+pageSize*(currentPage-1)}}</td>
            <td>{{item.name}}</td>
            <td>{{item.typeName()}}</td>
            <td>{{item.description}}</td>
            <td>
              <div class="template-operate-wrap">
                <a href="javascript:void(0)" v-on:click.stop="clickedOperate(index,$event)" data-oIndex=0></a>
                <a href="javascript:void(0)" v-on:click.stop="clickedOperate(index,$event)" data-oIndex=1></a>
                <a href="javascript:void(0)" v-on:click.stop="clickedOperate(index,$event)" data-oIndex=2></a>
                <a href="javascript:void(0)" v-on:click.stop="clickedOperate(index,$event)" data-oIndex=3></a>
              </div>
            </td>
          </tr>

        </table>
        <PageControl v-bind:current-page="currentPage" v-bind:total-page="totalPage"  v-on:clickedPrevious="clickedPrevious" v-on:clickedNext="clickedNext"/>
      </div>
    </div>
    <div style="width: 100%;height: 20px"></div>

  </div>
</template>

<script>

    import Location from '../../components/location/Location'

    import PageControl from '../../components/pageControl/PageControl'

    import {loadTemplateList,listOfPageWithPageSize,templateListOfKeywords,deleteTemplateOfId,deleteFlowOfId} from "./templateManager";

    import Toast from '../../components/toast/Toast';

    export default {
        name: "TemplateList",
        components:{
          PageControl
        },
      data:function () {
        return {
          show:false,
          templateList:null,
          currentTemplateList:null,
          currentPage:0,
          totalPage:0,
          pageSize:10,
          inputKeywords:null,
          searchKeywords:null,
        }
      },
      mounted:function(){

      },
      activated:function () {
        if (this.templateList==null){
          this.getTemplateList();
        }
      },
      methods:{


        /**
         * 
         *
         */
        searchTemplate:function(){

          let inputKeywords=this.inputKeywords;

          if (this.templateList) {

            let list = templateListOfKeywords(this.templateList,inputKeywords);

            if (!inputKeywords){

              list=this.templateList;
            }

            if (list.length) {

              this.currentPage = 1;

              this.totalPage=Math.ceil(list.length/this.pageSize);

              this.currentTemplateList=listOfPageWithPageSize(list,this.currentPage,this.pageSize);

            } else {

              this.currentPage = 0;

              this.totalPage=0;

              this.currentTemplateList=[];
            }

            this.searchKeywords=inputKeywords;
          }

        },


        /**
         * 
         *
         */
        clickedPrevious:function(){

          let inputKeywords=this.inputKeywords;

          let searchKeywords=this.searchKeywords;

          if (inputKeywords&&searchKeywords&&inputKeywords!=searchKeywords){

            this.searchTemplate();
          }
          else if (searchKeywords) {

            let list = templateListOfKeywords(this.templateList,inputKeywords);

            this.currentPage=Math.max(this.currentPage-1,1);

            this.currentTemplateList=listOfPageWithPageSize(list,this.currentPage,this.pageSize);

          }
          else{

            let list = this.templateList;

            this.currentPage=Math.max(this.currentPage-1,1);

            this.currentTemplateList=listOfPageWithPageSize(list,this.currentPage,this.pageSize);

          }

        },

        /**
         * 
         *
         */
        clickedNext:function(){

          let inputKeywords=this.inputKeywords;

          let searchKeywords=this.searchKeywords;

          if (inputKeywords&&searchKeywords&&inputKeywords!=searchKeywords){

            this.searchTemplate();

          }
          else if (searchKeywords) {

            let list = templateListOfKeywords(this.templateList,inputKeywords);

            this.currentPage=Math.min(this.currentPage+1,this.totalPage);

            this.currentTemplateList=listOfPageWithPageSize(list,this.currentPage,this.pageSize);

          }
          else{

            let list = this.templateList;

            this.currentPage=Math.min(this.currentPage+1,this.totalPage);

            this.currentTemplateList=listOfPageWithPageSize(list,this.currentPage,this.pageSize);

          }

        },

        /**
         * 
         *
         */
        getTemplateList:function(){

          let page=Math.max(this.currentPage,1);

          let pageSize=this.pageSize;

          const that=this;

          loadTemplateList(page,pageSize,function (isSuccess,list,baseResponse) {

            if (isSuccess){

              if (list.length){

                that.currentPage=1;

              }else {

                that.currentPage=0;
              }

              that.templateList=list;

              that.totalPage=Math.ceil(list.length/pageSize);

              that.show=true;

              that.currentTemplateList=listOfPageWithPageSize(list,1,pageSize);

            }
            else{

              let title = '';

              if (baseResponse && baseResponse.msg) {

                title = baseResponse.msg;
              }

              Toast.showToast(title);
            }
          });
        },

        /**
         * 
         *
         */
        clickedAddTemplate:function(){

          const that=this;

          let addTemplateCompletion=function (done) {

            that.$router.push({
              name:'templateList',
              params:{
                isBack:true
              }
            });

            if (done){
              that.inputKeywords=null;
              that.searchKeywords=null;
              that.getTemplateList();

            }

          }

          this.$router.push({
            name:'addTemplate',
            params:{
              addTemplateCompletion:addTemplateCompletion,
              title:''
            }
          });
        },

        /**
         * item
         * @param index index
         * @param event 
         */
        clickedOperate:function (index,event) {

          let oIndex=parseInt(event.target.dataset.oindex);

          switch (oIndex) {
            case 0:
              this.copyTemplate(index);
              break;

            case 1:
              this.editTemplate(index);
              break;

            case 2:
              this.deleteTemplate(index);
              break;

            case 3:
              this.templateDetail(index);
              break;

            default:
              break;
          }

        },

        /**
         * 
         *
         * @param index
         */
        copyTemplate:function (index) {

          let template=this.currentTemplateList[index];

          const that=this;

          let addTemplateCompletion=function (done) {

            that.$router.push({
              name:'templateList'
            });

            if (done){
              that.inputKeywords=null;
              that.searchKeywords=null;
              that.getTemplateList();

            }

          }

          this.$router.push({
            name:'addTemplate',
            params:{
              copyTemplate: template,
              addTemplateCompletion:addTemplateCompletion,
              title:''
            }
          });

        },

        /**
         * 
         *
         * @param index
         */
        editTemplate:function (index) {

          let template=this.currentTemplateList[index];

          this.$router.push({
            name:'editTemplate',
            params:{
              template:template
            }
          })

        },

        /**
         * 
         *
         */
        deleteTemplate:function (index) {

          let template=this.templateList[index];

          let sure=confirm(''+template.name+'?');

          if (!sure){
            return;
          }

          const that=this;

          deleteTemplateOfId(template.id,function (isSuccess,baseResponse) {

            if (isSuccess){

              that.templateList.splice(index+(that.currentPage-1)*that.pageSize,1);

              if (that.templateList.length==0){

                that.currentTemplateList=null;

                that.currentPage=0;

                that.totalPage=0;

              }
              else{

                that.totalPage=Math.ceil(that.templateList.length/that.pageSize);

                if (that.currentPage>that.totalPage){

                  that.currentPage=that.currentPage-1;

                }

                that.currentTemplateList=listOfPageWithPageSize(that.templateList,that.currentPage,that.pageSize);

              }
            }

          });
        },

        /**
         *
         * 
         */
        templateDetail:function(index) {

          let template=this.currentTemplateList[index];

          this.$router.push({
            name:'templateDetail',
            params:{
              template:template
            }
          })

        },
      }
    }
</script>

<style scoped>

  #templateContent{
    width: 100%;
  }

  .template-list-wrap{
    width: 100%;
    display: flex;
    min-width: 1000px;
    margin-top: 0px;
  }

  .template-list{
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
    background-color: white;
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
    padding: 15px 20px 50px;
  }


  .template-list-title{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #0087E7;
    letter-spacing: 0;
    line-height: 30px;
    margin-bottom: 0px;
  }

  .btn-add-template{
    width: 120px;
    height: 30px;
    background: #0087E7;
    border-radius: 15px;
    margin-top: 20px;
    margin-bottom: 15px;
    border: none;
    outline: none;
    padding: 0px 0px;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: 0;
  }

  .btn-add-template:hover{
    background-color: #006eff;
    cursor: pointer;
  }

  .btn-add-template{
    background-color: #0087E7;
  }

  .input-search-template{
    width:400px;
    height: 30px;
    color: black;
    background-color: #F4F4F4;
    border-radius: 15px;
    border: solid transparent 0px;
    margin-left: 15px;
    margin-bottom: 15px;
    padding: 0px 20px 0px 37px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    letter-spacing: 0;
    background-image: url("../../assets/icon_search.png");
    background-repeat: no-repeat;
    background-position-x: 13px;
    background-position-y: center;
    box-sizing: border-box;
    display: inline;
  }

  .input-search-template::-webkit-input-placeholder{
    color: #BBBBBB;
  }

  .template-list table{
    width: 100%;
    border-collapse:collapse;
  }

  .template-list table,.template-list td,.template-list th{
    border:1px solid #E4E4E4;
  }

  .template-list table tr{
    text-align: center;
  }

  .template-list table tr:nth-child(1){
    height: 40px;
    line-height: 40px;
  }

  .template-list table tr:nth-child(n+2){
    height: 40px;
  }

  .template-list table th{
    text-align: center;
  }

  .template-list table th:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
  }

  .template-list table th{
    flex: 1;
    background: #F4F4F4;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #676767;
    text-align: center;
  }

  .template-list table th:nth-child(n+4){
    flex: 3;
  }

  .template-list table th:nth-child(n+5){
    flex: 2;
  }

  .template-list table td{
    text-align: center;
  }

  .template-list table td:nth-child(n+2){
    text-align: left;
    padding-left: 15px;
  }

  .template-list table td{
    flex: 1;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #0C101B;
    letter-spacing: 0;
    line-height: 40px;
  }

  .template-list table td:nth-child(n+4){
    flex: 3;
  }

  .template-list table td:nth-child(n+5){
    flex: 2;
  }

  .template-list .template-operate-wrap{
    display: flex;
  }

  .template-operate-wrap a{
    color: #0087E7;
    text-decoration: none;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    letter-spacing: 0;
  }

  .template-operate-wrap a:nth-child(1){
    padding: 0px 0px;
  }

  .template-operate-wrap a:nth-child(n+2){
    padding-left: 30px;
  }
</style>



