import Vue from 'vue'
import VueRouter from 'vue-router'
import Admin from './Admin.vue'
import TemplateModule from '../../views/template/Template'
import TemplateList from '../../views/templateList/TemplateList'
import AddTemplate from '../../views/addTemplate/AddTemplate'
import TemplateEdit from '../../views/templateEdit/templateEdit'
import TemplateDetail from '../../views/templateDetail/templateDetail'
import Flow from '../../views/flow/Flow'
import Team from '../../views/team/Team'

console.log('admin.js');

Vue.config.productionTip = false

Vue.use(VueRouter)

Vue.config.productionTip = false

let router = new VueRouter({
  routes: [
    {
      path: '/template',
      component: TemplateModule,
      children:[{
          path:'templateList',
          name:'templateList',
          component:TemplateList,
        },
        {
          path:'addTemplate/addFlow',
          name:'addFlow',
          component:Flow,
        },
        {
          path:'editTemplate',
          name:'editTemplate',
          component:TemplateEdit
        },
        {
          path:'templateDetail',
          name:'templateDetail',
          component:TemplateDetail
        },
        {
          path:'addTemplate',
          name:'addTemplate',
          component:AddTemplate,
        },
        {
          path:'',
          redirect:'templateList',
      }]
    },
    {
      path: '/team',
      name:'team',
      component: Team
    },
    { path: '/*',
      redirect:'/template/templateList'
    },
    { path: '',
      redirect:'/template/templateList'
    }],
  scrollBehavior:function (to,from,savedPosition) {
    // savedPosition / 
    if (savedPosition){
      return savedPosition
    }
    else {
      return {x:0,y:0}
    }

  },
  base:'/'
})

/* eslint-disable no-new */
new Vue({
  el: '#admin',
  router,
  components: { Admin },
  template: '<Admin/>'
})



